import * as React from 'react';
import { Container, Typography, Grid, Box } from '@mui/material';
import FormKYC from 'src/views/vleppo/KYC/FormKyc';
import Page from 'src/components/Page';
import { Redirect, useLocation } from 'react-router-dom';
import useAuth from 'src/hooks/useAuth';

const style = {
  verifyText: {
    color: '#22C55E'
  },
  fontWeightBold: {
    fontWeight: 'bold'
  },
  backgroundWhite: {
    backgroundColor: '#fff'
  },
  errorInfo: {
    backgroundColor: '#168bc226',
    borderRadius: '5px'
  },
  container: {
    width: '100%',
    maxWidth: 360,
    bgcolor: 'background.paper'
  },
  uploadBox: {
    backgroundColor: 'rgba(22, 139, 194, 0.1)',
    border: '1px dashed #168BC2',
    boxSizing: 'border-box',
    borderRadius: '10px'
  }
};

const KYC = () => {
  const location = useLocation();

  const { isAuthenticated } = useAuth();
  if (isAuthenticated !== null && isAuthenticated === false) {
    const system = location.pathname.split('/')[1];
    const returnUrl = new URLSearchParams(location.search).get('returnUrl');
    return returnUrl ? <Redirect to={returnUrl} /> : <Redirect to={`/${system}/login?returnUrl=/${system}/kyc`} />;
  }
  return (
    <Container maxWidth="lg">
      <Page title="KYC">
        <Grid container>
          <Grid item xs={12} md={12}>
            <Box p={2}>
              <Box style={style.backgroundWhite} p={3}>
                <Typography variant="h5" component="div" style={style.fontWeightBold}>
                  Know Your Customer Verification
                </Typography>
                <FormKYC />
              </Box>
            </Box>
          </Grid>
        </Grid>
      </Page>
    </Container>
  );
};

export default KYC;
