import { useState, useEffect, useRef } from 'react';
import { Box, IconButton } from '@mui/material';
import LoadingButton from '@mui/lab/LoadingButton';
import { Close as CloseIcon } from '@mui/icons-material';
import { useDropzone } from 'react-dropzone';
import axios from 'src/utils/api';

const ImageUpload = ({ onUrlChange }) => {
  const [isLoading, setIsLoading] = useState(false);
  const [url, setUrl] = useState();

  const [isInputMounted, setIsInputMounted] = useState(false);
  const inputRef = useRef();

  useEffect(() => {
    if (!isInputMounted) {
      setIsInputMounted(true);
    }
  }, [isInputMounted]);

  useEffect(() => {
    onUrlChange(url);
  }, [url]);

  const { getRootProps, getInputProps } = useDropzone({
    accept: 'image/*',
    noDrag: true,
    onDrop: (acceptedFiles) => {
      const file = acceptedFiles[0];
      if (!file) return;

      setIsLoading(true);
      const formData = new FormData();
      formData.append('file', file);
      axios
        .post('/upload/image', formData)
        .then(({ data }) => setUrl(data.data))
        .finally(() => setIsLoading(false));
    }
  });

  const handleDelete = () => {
    setUrl(null);
    setIsInputMounted(false);
  };

  return (
    <Box display="flex" flexDirection="column" alignItems="center">
      <Box position="relative" mb={3}>
        {!!url ? (
          <>
            <img style={{ width: '100%', height: '100%', minHeight: '62.25px', objectFit: 'contain' }} src={url} />
            <IconButton sx={{ position: 'absolute', top: -18, right: -18 }} onClick={handleDelete}>
              <CloseIcon fontSize="small" style={{ fill: 'red' }} />
            </IconButton>
          </>
        ) : (
          <img style={{ display: 'inline-block', width: '60px', minHeight: '62.25px' }} src="/static/icons/kyc-image.png" />
        )}
      </Box>

      <div {...getRootProps()}>{isInputMounted && <input {...getInputProps()} ref={inputRef} />}</div>
      <LoadingButton variant="outlined" sx={{ width: 150 }} loading={isLoading} onClick={() => inputRef.current.click()}>
        Upload
      </LoadingButton>
    </Box>
  );
};

export default ImageUpload;
